import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/projects/:project',
      redirect: to => {
				const { params } = to
				window.location.replace(`/static/projects/${params.project}/`)
      }
    }
  ]
})



