<template>
  <div id="moleys">
    <audio autoplay ref="music" v-on:ended="$emit('thatPartIsOverNow')">
      <source type="audio/mpeg" src="../../assets/audio/fishmoley.mp3">
    </audio>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
    <span><i></i></span>
  </div>
</template>

<script>
export default {
  name: 'moley'
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
/* moley animations */

#moleys {
  position: fixed;
  width: 100%;
  z-index: 100;
}

span {
  display: inline-block;
  animation-timing-function: linear;
  z-index: 1;
}

i {
  display: inline-block;
  background: url(../../assets/graphics/moley.png) no-repeat;
  z-index: 1;
  background-size: 100%;
  transform-style: preserve-3d;
  animation: thump 1s infinite;
  height: 100%;
  width: 100%;
}

span:nth-of-type(n)    { position: absolute; top: -10em; height: 10em; width: 10em; }
span:nth-of-type(2n+1) { position: absolute; top: -15em; height: 15em; width: 15em; }
span:nth-of-type(3n+2) { position: absolute; top: -20em; height: 20em; width: 20em; }

span:nth-of-type(n)  { animation: falling 3s 0s infinite }
span:nth-of-type(2n) { animation: falling2 4s 0s infinite }
span:nth-of-type(3n) { animation: falling3 3s 0s infinite }


span:nth-of-type(n)   { animation-delay: 1.25s; right:50vw;  }
span:nth-of-type(2n)  { animation-delay: 2s; right:13.33vw; }
span:nth-of-type(3n)  { animation-delay: 2.3s; right:17vw; }
span:nth-of-type(4n)  { animation-delay: 4.4s; right:25vw; }
span:nth-of-type(5n)  { animation-delay: 5s;   right:33.33vw; }
span:nth-of-type(6n)  { animation-delay: 3.5s; right:40vw; }
span:nth-of-type(7n)  { animation-delay: 2.8s; right:46.66vw; }
span:nth-of-type(8n)  { animation-delay: 2.75s; right:53.33vw; }
span:nth-of-type(9n)  { animation-delay: 3.3s; right:60vw; }
span:nth-of-type(10n) { animation-delay: 2.5s; right:66.66vw; }
span:nth-of-type(11n) { animation-delay: 3.2s; right:73.33vw; }
span:nth-of-type(12n) { animation-delay: 4.1s; right:80vw; }
span:nth-of-type(13n) { animation-delay: 2.5s; right:43vw; }
span:nth-of-type(14n) { animation-delay: 4.7s; right:57vw; }
span:nth-of-type(15n) { animation-delay: 3s;   right:55vw; }


@keyframes falling {  
  0% {
    -webkit-transform:
      translate3d(0,0,0)
      rotate(0deg);
  }
  
  95% {
    opacity: 1;
  }
   
  100% {
    -webkit-transform:
      translate3d(0,250vh,0)
      rotate(1080deg);
    opacity: 0;
  }
}

@keyframes falling2 {
  0% {
    -webkit-transform:
      translate3d(0,0,0)
      rotate(90deg);
  }
  
  95% {
    opacity: 1;
  }

  100% {
    -webkit-transform:
      translate3d(100%,245vh,0)
      rotate(720deg);
    opacity: 0;
  }
}

@keyframes falling3 {
  0% {
    -webkit-transform:
      translate3d(50%,0,0)
      rotate(-20deg);
  }

  95% {
    opacity: 1;
  }
  
  100% {
    -webkit-transform:
      translate3d(50%,235vh,0)
      rotate(-720deg);
    opacity: 0;
  }
}

@keyframes thump {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.5);
  }

  50% {
    transform: scale(1);
  }

  75% {
    transform: scale(1.5);
  }

  100% {
    transform: scale(1);
  }
}
</style>



