<template>
    <div class="blog-list">
        <div class="header"><h3>Write Ups</h3> by <a href="https://medium.com/@Gregles" title="@Gregles on medium">@Gregles</a></div>
        <a href="https://github.com/seattle-imtech/seattle-vr/tree/master/community-survey/2018/">
          <div class="blog-post">
            <h4>Seattle VR 2018 Community Survey Results/Review</h4>
            <p>A review and analysis of the Seattle VR 2018 Community Survey we conducted to better understand and serve the local VR community.</p>
          </div>
        </a>
        <a href="https://medium.com/cinematicvr/lessons-learned-in-360-degrees-98b12d4cb134">
          <div class="blog-post">
            <h4>Lessons Learned in 360 Degrees</h4>
            <p>During a trip to Japan, I was able to experiment with 360Rize’s Pro6 360 degree camera rig. Though I didn’t quite get the quality of captures I was hoping for, I still ended up learning a lot about the process of capturing and creating 360 degree video. This is in no way a complete list of tips for shooting in 360 degrees. There are already many blogs, from far more qualified writers about that. This should be considered more an addendum specific to my goals and my experiences.</p>
          </div>
        </a>
        <a href="http://streamersquare.com/stylesheet-embedding-twitch-streams/">
          <div class="blog-post">
            <h4>Streamer Square: Embedding Responsive Twitch Streams</h4>
            <p>A guide to embedding a twitch stream/chat in a responsive 16:9 container I wrote up for the streaming news site StreamerSquare.com.</p>
          </div>
        </a>
        <a href="https://jcrastelli.wordpress.com/2013/07/25/tic-tac-toe-devlopment-walkthrough/">
          <div class="blog-post">
            <h4>Android: Building Tic-Tac-Toe</h4>
            <p>An old guide for building Tic-Tac-Toe I wrote up several years back. The over-all project is outdated at this point, but the AI and game logic is still worthwhile. The Tic-Tac-Toe ruleset isn't exactly evolving anytime soon.</p>
          </div>
        </a>
      </div>
</template>

<script>
export default {
  name: 'bloglist',
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';

  .blog-list {
    flex: 4;
    display: flex;
    flex-direction: column;
    color: #F5F8FA;
    margin-bottom: 5em;
    .header {
      padding: 10px 1em;
      border-bottom: 1px solid rgba(145, 165, 180, 0.6);
      font-size: 12px;
      font-weight: 400;
      line-height: 16px;
      a {
        color: #1b95e0;
      }
    }
    .blog-post {
      padding: .5em 1.5em 1.5em 1em;
      &:hover {
        background-color: rgba(25,30,35,.61);
      }
      border-bottom: 1px solid rgba(145, 165, 180, 0.6);
    }
    a {
      color: inherit;
      text-decoration: none;
      color: #F5F8FA;
    }
    h3 {
      margin: 0;
      display: inline-block;
      font-weight: 300;
      line-height: 24px;
      font-size: 21px;
    }
    h4 { 
      color: $palette-mid-tone;
      padding: .5em 0;
      margin: 0;
      font-size: 1.25em;
      &:hover {
        color: #a7bbb1;
      }
    }
    span { display: inline-block; border-bottom: 1px solid $font-white; }
    p { margin: 0; }
  }
</style>



